@use "./mixins";
@use "./vers";

.contact {
  .mw__wrap {
    position: relative;
    margin-left: auto;
    margin-right: auto;
    max-width: 780px;
  }
  .section__sub {
    margin-top: 4em;
    text-align: center;
    font-weight: 400;
    span {
      display: block;
    }
    .main {
      font-size: 26px;
    }
    .sub {
      color:#999;
    }
  }
  .items__wrap {
    width: 100%;
    margin-top: 4em;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 55px 4%;
    @include mixins.mqSP {
      display: block;
    }
  }
  .item_text {
    @include mixins.mqPC {
      width: 46%;
    }
    margin-bottom: 2em;
  }
  .title {
    font-size: 16px;
    font-weight: 700;
    @include mixins.mqSP {
      font-size: 14px;
    }
  }
  .role {
    padding-top: 0.5em;
    font-size: 13px;
    @include mixins.mqSP {
      font-size: 12px;
    }
  }

  .all_item_link {
    margin-top: 120px;
    text-align: center;
    @include mixins.mqSP(){
      margin-top: 60px;
    }
    a {
      display: inline-block;
      border: 1px solid #333;
      background-color: #333;
      padding: 2em 0;
      color: #fff;
      width: 100%;
      line-height: 1.2;
      text-decoration: none;
      @include mixins.mqSP(){
        display: block;
        padding: 2em 0;
      }
    }
  }

}
