@use "./mixins";
@use "./vers";

// _top

.hero {
  z-index: 10;
  position: relative;
  height: 90vh;
  min-height: 630px;
  width: 100%;
  background-image: url("/assets/images/xhro_4ch_visual.jpg");
  background-size: cover;
  background-position: 50% 50%;
  @include mixins.mqSP{
    background-size: 100% auto;
    background-position: 50% 100%;

  }
  .mw__wrap {
    height: 100%;
    background-color: rgba(#000000, 0.4);
  }
}
.main_symbols {
  width: 360px;
  position: relative;
  padding-top: 14vw;
  margin-left: auto;
  margin-right: auto;
  @include mixins.mqSP{
    width: 60vw;
    margin-left: 8%;
    padding-top: 20vw;
    svg {
      width: 100%;
      height: auto;
    }
  }
}

.header__title {}
.header__caption {
  position: relative;
  width: auto;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  font-weight: 400;
  color: #fff;
  letter-spacing: 0.1em;
  line-height: 1.4;
  padding-left: 8%;
  padding-right: 8%;
  @include mixins.mqSP{
    text-align: left;
  }

  &.lang_en {
    font-size: 22px;
    padding-top: 4vw;
    @include mixins.mqSP{
      padding-top: 10vw;
      font-size: 15px;
    }
  }
  &.lang_jp {
    padding-top: 2em;
    font-size: 17px;
    @include mixins.mqSP{
      font-size: 14px;
    }
  }


}

@keyframes fadeIn {
	0% { opacity:0; transform: translateY(10px);}
	100% { opacity:1; transform: translateY(0);}
}
