@use "./mixins";
@use "./vers";

.get-starting {
  .mw__wrap {
    position: relative;
    margin-left: auto;
    margin-right: auto;
    max-width: 1280px;
  }
  .sauna_visual__wrap {
    position: relative;
  }
  .note {
    text-align: center;
    font-size: 12px;
    padding-top: 2em;
    line-height: 1.2;
  }
  .fig_wrap + .fig_wrap {
    padding-top: 4em;
  }
  .inner__wrap + .fig_wrap {
    padding-top: 4em;
  }
  .section__def {
    margin-top: 1em;
    font-size: 90%;
    li {
      display: inline-block;
      margin-top: 0.6em;
      border-left: 2px solid #333;
      padding-left: 1em;
      line-height: 1.2;
    }
    .strong {
      display: inline-block;
      padding-right: 0.5em;
      font-weight: 700;
    }
  }
  .lang_jp {
    color:#999;
    text-align: justify;
  }
  .inner__wrap {
    margin-top: 3em;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    max-width: 780px;
  }
  .link {
    padding-top: 4em;
    width: 100%;
    a {
      width: 100%;
      display: block;
      padding-top: 1em;
      padding-bottom: 1em;
      text-decoration: none;
      border: 1px solid #333;
      text-align: center;
    }
  }
  .reference_head {
    margin-top: 4em;
    margin-bottom: 1em;
  }
  .reference_list {
    font-size: 11px;
    line-height: 1.4;
  }
  .items__wrap {
    width: 100%;
    margin-top: 6em;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 40px 4%;
    @include mixins.mqSP {
      display: block;
    }
  }
  .item__wrap {
    display: flex;
    width: 46%;
    align-items:center;
    @include mixins.mqSP {
      margin-top: 1em;
      width: 100%;
    }
    .item_img {
      width: 26%;
    }
    .item_text {
      width: 68%;
      padding-left: 6%;
    }
    .name {
      font-size: 18px;
      @include mixins.mqSP {
        font-size: 14px;
        font-weight: 700;
      }
    }
    .title {
      font-size: 16px;
      @include mixins.mqSP {
        font-size: 14px;
      }
    }
    .role {
      padding-top: 0.5em;
      font-size: 13px;
      @include mixins.mqSP {
        font-size: 12px;
        line-height: 1.3;
      }
    }
  }
  .item_text {
    dt,dd {
      display: block;
    }
  }
}
